@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");

html {
  color: black;
}

a {
  text-decoration: none;
  color: black;
}

::-webkit-scrollbar {
  display: none;
}

.fadeIn {
  animation: fadeIn 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10%);
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

.fadeInLeft {
  animation: fadeInLeft 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(10%);
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.fadeInBasic {
  animation: fadeInBasic 2s;
  opacity: 1;
}

@keyframes fadeInBasic {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.app {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  line-height: 150%;
}

/* 
=================
BLOBS
=================
*/

@import url("https://fonts.googleapis.com/css?family=Exo:400,700");

.area {
  background: rgba(255, 255, 255, 0.5);
  width: 100%;
  height: 100%;
  z-index: -1000;
  position: absolute;
  margin: 0;
  bottom: 0;
  padding: 0;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.circles li {
  margin: 0;
  padding: 0;
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(184, 77, 77, 0.446);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 10%;
  }

  100% {
    transform: translateY(-1400px) rotate(720deg);
    opacity: 0;
    border-radius: 90%;
  }
}
