@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  color: black;
}

a {
  text-decoration: none;
  color: black;
}

::-webkit-scrollbar {
  display: none;
}

.fadeIn {
  animation: fadeIn 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10%);
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

.fadeInLeft {
  animation: fadeInLeft 2s;
  opacity: 1;
  position: relative;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(10%);
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.fadeInBasic {
  animation: fadeInBasic 2s;
  opacity: 1;
}

@keyframes fadeInBasic {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.app {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  line-height: 150%;
}

/* 
=================
BLOBS
=================
*/

.area {
  background: rgba(255, 255, 255, 0.5);
  width: 100%;
  height: 100%;
  z-index: -1000;
  position: absolute;
  margin: 0;
  bottom: 0;
  padding: 0;
}

.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.circles li {
  margin: 0;
  padding: 0;
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(184, 77, 77, 0.446);
  animation: animate 25s linear infinite;
  bottom: -150px;
}

.circles li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.circles li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 10%;
  }

  100% {
    transform: translateY(-1400px) rotate(720deg);
    opacity: 0;
    border-radius: 90%;
  }
}

a {
  font-style: normal;
}

.homePage {
  display: flex;
  width: 60%;
  margin: 5em 0;
  font-family: "Raleway", sans-serif;
}

.homePage::-webkit-scrollbar {
  display: none;
}

.siteNavigation {
  display: flex;
  flex-direction: column;
  width: 35%;
}

.socialMediaLinksList {
  margin-top: 0;
  padding-left: 0;
  list-style-type: none;
}

.socialMediaListItem {
  width: -webkit-max-content;
  width: max-content;
  margin-top: 0.5em;
  margin-right: 1em;
}

.socialMediaLinksList:nth-child(1) {
  margin-top: 0;
}

.socialMediaLinksList a {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}

.socialMediaIcon {
  margin-right: 0.1em;
  width: 16px;
  height: 16px;
  font-weight: bold;
}

.pageLinks {
  padding-left: 0;
  list-style-type: none;
  font-size: 20px;
}

.pageLinks li:nth-child(2) {
  margin-top: 0.5em;
}

.pageLinks li {
  width: 90%;
}

/*y
=================
DISPLAYED CONTENT
=================
*/

.displayedPage {
  width: 65%;
  overflow-x: hidden;
}

/*
=================
LAYOUT CHANGES
=================
*/

@media only screen and (max-width: 1100px) {
  .homePage {
    width: 80%;
    flex-direction: column;
  }
  .siteNavigation {
    border-right: none;
    border-bottom: 1px solid black;
    width: 100%;
  }

  .socialMediaListItem {
    display: inline-block;
  }
  .pageLinks li {
    width: 100%;
  }
  .displayedPage {
    width: 100%;
  }
  ::-webkit-scrollbar {
    display: none;
  }
}

@media only screen and (min-width: 1700px) {
  .homePage {
    width: 45%;
  }
}

/* BLACK UNDERLINE ANIMATION */

.aboutMeList a,
.projectLink,
.socialMediaLinksList a {
  font-style: italic;
  position: relative;
}

.aboutMeList a:hover,
.projectLink:hover,
.socialMediaLinksList a:hover {
  font-weight: 900;
}

.aboutMePage a:before,
.projectLink:before,
.socialMediaLinksList a:before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background: black;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}
.aboutMePage a:hover:before,
.projectLink:hover:before,
.socialMediaLinksList a:hover:before {
  visibility: visible;
  transform: scaleX(1);
}

/* PAGE LINK (SCALE/GREY COVER) ANIMATION */

.pageLinks li,
.projectsList li {
  padding: 0.2em;
  border-radius: 0.3em;
  transition: 0.5s ease-in-out;
  background: linear-gradient(
    to left,
    rgba(255, 255, 255, 0) 50%,
    rgba(211, 211, 211, 0.52) 50%
  );
  background-size: 200% 100%;
  background-position: right bottom;
}

.pageLinks li:hover,
.projectsList li:hover {
  background-position: left bottom;
  transform: scale(1.02);
  cursor: pointer;
  font-weight: 900;
  z-index: 9999;
}

.aboutMePage {
  display: flex;
  flex-direction: column;
  padding-left: 1em;
  border-left: 1px solid black;
  height: -webkit-max-content;
  height: max-content;
}

-webkit-scrollbar {
  display: none;
}

.aboutMePage h2 {
  margin-bottom: 0;
}

.aboutMeList {
  padding-left: 1em;
}

.aboutMeList li {
  margin-top: 1em;
}

.aboutMeList li ul li {
  margin-top: 0;
}

.odinProjectOutline {
  margin-top: 1em;
}

.projectRef {
  font-weight: bold;
  font-style: italic;
}

@media only screen and (max-width: 1100px) {
  .aboutMePage {
    border-left: none;
  }
}

@media only screen and (max-width: 450px) {
  .odinProjectOutline {
    padding-left: 0;
  }
}

.projectsPage {
  display: flex;
  flex-direction: column;
  padding-left: 1em;
  line-height: 150%;
  border-left: 1px solid black;
  width: 100%;
  overflow-x: hidden;
}

.projectsList {
  padding-left: 0;
  list-style-type: none;
}

.projectsList li {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 1em;
}

.projectName,
.projectLanguage {
  display: flex;
  width: 35%;
  display: flex;
  align-items: center;
}

.projectLanguage {
  margin-right: 1em;
}

/*
=================
INDIVIDUAL PROJECTS
=================
*/

.projectKeyFeatures {
  list-style: none;
  padding-left: 0;
}

.projectKeyFeatures li {
  width: 100%;
}

.projectKeyFeatures li:before {
  content: "✓";
  display: inline-block;
  padding-right: 0.2em;
}

.divider {
  width: 50%;
  margin: 5px auto;
  bottom: 50px;
  border: 1px solid black;
}

.projectLink {
  margin: 0 auto;
  text-align: center;
  white-space: nowrap;
  position: relative;
}

@media only screen and (max-width: 1100px) {
  .projectsPage {
    border-left: none;
  }
  .divider {
    width: 60%;
  }
}

